<template>
  <Fragment>
    <div class="cart_view" :class="[currentCartActive ? 'open' : 'close']">
      <div class="cart_confirm ">
        <div class="list" v-for="item in cart" :key="item.id">
          <div class="item item_img">
            <img :src="item.img" alt="" />
          </div>
          <div class="item item_des">
            <div class="wrap">
              <h3 class="item_des_title">
                {{ item.title }} -
                <span class="color">{{ item.quantity }}</span>
              </h3>
              <span class="item_des_info"
                ><span
                  class="item_color"
                  :class="[selectShaveColorClass]"
                  v-if="item.id === 1"
                  >{{ selectShaveColorName }}</span
                >{{ item.des }}</span
              >
              <div class="item_des_price">
                <span class="cycle">{{
                  item.option ? item.option.name : " "
                }}</span>
                <span class="price">{{ item.price.toLocaleString() }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="total_view">
        <div class="info">
          <span class="left"></span>
          <span class="right">{{ getTotalPrice.toLocaleString() }}</span>
        </div>
        <div class="info">
          <span class="left"></span>
          <span class="right"></span>
        </div>
        <div class="info t_expected">
          <span class="left">   </span>
          <span class="right">{{ getTotalPrice.toLocaleString() }}</span>
        </div>
      </div>
    </div>
    <div
      class="overlay_bg confirm"
      :class="[currentCartActive ? 'open' : 'close']"
      @click="closeCartView"
    ></div>
  </Fragment>
</template>

<script>
import { Fragment } from "vue-fragment";
export default {
  name: "CartConfirm",
  props: {
    currentCartActive: {
      type: Boolean,
    },
  },
  components: {
    Fragment,
  },
  data() {
    return {};
  },
  computed: {
    cart() {
      return this.$store.state.cart;
    },
    //  
    getTotalPrice() {
      return this.$store.getters.total_price;
    },
    //   
    selectShaveColorName() {
      let colorName = [" ", " ", " "];
      return colorName[this.$store.state.shave_color - 1];
    },
    //   
    selectShaveColorClass() {
      let colorClass = ["navy", "blue", "gray"];
      return colorClass[this.$store.state.shave_color - 1];
    },
  },
  methods: {
    closeCartView() {
      this.$emit("update:currentCartActive", false);
    },
  },
};
</script>

<style></style>
